import uniList from '@/components/uni/uni-list/uni-list.vue'
import uniListItem from '@/components/uni/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			tagList:[{
				title:'aa'
			}],
			tag_show: 1,
			id:'0',
			new_tag:{
				id:'',
				title:'',
			},
			tag_title:''
		}
	},
	methods: {
		//----------------------------------------------------添加标签
		add_tag() {
			this.tag_show = 0
		},
		sub_add(){
			if(!this.tag_title){
				this.$api.msg('标签名不能为空')
				return;
			}
			let id = this.id
			let new_tag = {
				id:id++,
				title:this.tag_title
			}
			this.tagList.push(new_tag)
			this.tag_title = ''
			this.id = id
			this.tag_show = 1
			this.$api.msg('添加成功')
			console.log(this.tagList)
		},
	},
	onPullDownRefresh() {
	
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
